<template>
  <div class="index-wrap">
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        shape="round"
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <van-row>
      <van-col span="5">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item 
            :title="item.name"
            v-for="(item, index) in sideBars" 
            :key="index"
          />
        </van-sidebar>
      </van-col>
      <van-col span="19">
        <van-grid :gutter='8'>
          <van-grid-item
            v-for="(item, index) in leftSection" 
            :key="index"
            :text="item.name"
          >
            <van-image :src="item.url" />
          </van-grid-item>
        </van-grid>
        <van-divider />
        <van-list>
          <van-cell v-for="(item, index) in list" :key="index" class="list">
            <div class="title">{{item.title}}</div>
            <div class="desc">
              <span :class="['words', spreadArray[index] === false ? 'more' : '']">
                {{item.desc}}
              </span>
              <van-icon
                class="spread"
                v-if="spreadArray[index] === true"
                name="ellipsis" 
                size="20"
                @click="spreadDesc(index)"
              />
            </div>
            <div>
              <span class="fl price">¥{{item.price}}</span>
              <van-icon 
                class="fr" 
                v-if="item.star === true"
                name="star-o" 
                color="#07c160"
                size="20"
              />
              <van-icon 
                class="fr" 
                v-else
                name="star" 
                color="#07c160"
                size="20"
              />
            </div>
          </van-cell>
        </van-list>
      </van-col>
    </van-row>
    <TabBar />
  </div>
</template>

<script>
import TabBar from '@/components/TabBar'

export default {
  name: 'points',
  components: {
    TabBar
  },
  data () {
    return {
      value: '',
      activeKey: 0,
      error: false,
      loading: false,
      spreadArray: [],
      sideBars: [
        { name: '快销推荐', id: 1 },
        { name: '商超购物', id: 2 },
        { name: '影视下载', id: 3 },
        { name: '游戏点卡', id: 4 },
        { name: '甜品饮品', id: 5 },
        { name: '生活服务', id: 6 },
        { name: '美食生鲜', id: 7 },
        { name: '运动出行', id: 8 },
        { name: '音乐读书', id: 9 },
        { name: '我的收藏', id: 10 }
      ],
      leftSection: [
        { name: '爱奇艺视频', id: 1, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '票神', id: 2, url: 'http://image.99shou.cn/self/coupons/logos/票神.png!img' },
        { name: '腾讯视频', id: 3, url: 'http://image.99shou.cn/self/coupons/logos/腾讯.png!img' },
        { name: '百度网盘', id: 4, url: 'http://image.99shou.cn/self/coupons/logos/百度网盘.png!img' },
        { name: '咪咕阅读', id: 5, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '芒果视频', id: 6, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '美食视频通兑券鲜', id: 7, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '查看更多', id: 8, url: 'http://quanma51.com/wap/dist/images/7d71a329.more.png' },
      ],
      list: []
    }
  },
  computed: {
  },
  created () {
    this.initList()
  },
  methods: {
    initList () {
      let list = [
        { 
          title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30平安RUN、每日10点光大银行15买30平安RUN、每日10点光大银行15买30', 
          id: 1,
          price: 25,
          star: true
        },
        { 
          title: '25收百果园30代金券',
          desc: '银联*平安*光大*平安RUN', 
          price: 25,
          id: 2 
        },
        { 
          title: '25收百果园30代金券',
          desc:'银联*平安*光大', 
          price: 25,
          id: 3 
        },
        { title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30', 
          price: 25,
          id: 4
        },
        { title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30', 
          price: 25,
          id: 5
        }
      ]

      this.list = list

      for (let i = 0; i < list.length; i++) {
        const el = list[i]

        if (el.desc.length >= 20) {
          this.spreadArray[i] = true
        } else {
          this.spreadArray[i] = false
        }
      }
    },
    onSearch(val) {
      this.$toast(val)
    },
    onCancel() {
      this.$toast('取消')
    },
    spreadDesc (index) {
      this.spreadArray[index] = false
      console.log(this.spreadArray)
    }
  }
}
</script>



